import EventEmit from './event/emit';

class CanvasElement extends EventEmit {
  constructor(ctx) {
    super();
    this.context = ctx;
    // canvas (width/height) * pixelRatio
    this.width = 0;
    this.height = 0;
    this.style = {};
    this.currentStyle = {};
    // CanvasElement
    this.isCanvasElement = true;
  }

  getContext(/* type */) {
    return this.context;
  }

  getBoundingClientRect() {
    const width = this.width;
    const height = this.height;
    // 
    return {
      top: 0,
      right: width,
      bottom: height,
      left: 0
    };
  }

  addEventListener(type, listener) {
    this.on(type, listener);
  }

  removeEventListener(type, listener) {
    this.off(type, listener);
  }

  dispatchEvent(type, e) {
    this.emit(type, e);
  }
}

function supportEventListener(canvas) {
  if (!canvas) {
    return false;
  }
  //  HTMLCanvasElement
  if (canvas.nodeType !== 1 || !canvas.nodeName || canvas.nodeName.toLowerCase() !== 'canvas') {
    return false;
  }
  // canvas.getContext('2d')CanvasRenderingContext2D
  // ctx.canvas, nodeType1addEventListener
  let support = false;
  try {
    canvas.addEventListener('eventTest', () => {
      support = true;
    });
    canvas.dispatchEvent(new Event('eventTest'));
  } catch (error) {
    support = false;
  }
  return support;
}


export default {
  create(ctx) {
    if (!ctx) {
      return null;
    }
    if (supportEventListener(ctx.canvas)) {
      return ctx.canvas;
    }
    return new CanvasElement(ctx);
  }
};
