import { getScale } from '../helper';
import { each, isString, isObject, toTimeStamp } from '../../util/common';

function updateLinearScale(field, min, max) {
  const chart = this.chart;
  const scale = getScale(chart, field);
  // const colDef = Helper.getColDef(chart, field);
  // chart.scale(field, Util.mix({}, colDef, {
  //   min,
  //   max,
  //   nice: false
  // }));
  scale.change({
    min,
    max,
    nice: false
  });
}


function updateCatScale(field, newValues, ticks, values, minIndex, maxIndex) {
  const chart = this.chart;
  // const colDef = Helper.getColDef(chart, field);
  const scale = getScale(chart, field);
  scale.change({
    values: newValues,
    ticks,
    scale(value) {
      if (this.type === 'timeCat') {
        value = toTimeStamp(value);
      }
      const rangeMin = this.rangeMin();
      const rangeMax = this.rangeMax();
      const range = rangeMax - rangeMin;
      let min;
      let max;
      let percent;
      const currentIndex = values.indexOf(value); // 
      if (currentIndex >= 0 && currentIndex < minIndex) { // 
        max = rangeMin > 0 ? -0.1 : rangeMin - 0.1;
        min = max - range;
        percent = currentIndex / minIndex;
      } else if (currentIndex >= 0 && currentIndex > maxIndex) { // 
        min = rangeMax < 1 ? 1.1 : rangeMax + 0.1;
        max = min + range;
        percent = (currentIndex - maxIndex - 1) / (values.length - 1 - maxIndex);
      } else { //  this.values 
        const index = this.translate(value);

        if (this.values.length === 1) {
          percent = index;
        } else {
          percent = (index) / (this.values.length - 1);
        }

        min = rangeMin;
        max = rangeMax;
      }
      return min + percent * (max - min);
    },
    getTicks() {
      const self = this;
      const ticks = this.ticks;
      const rst = [];
      each(ticks, tick => {
        let obj;
        if (isObject(tick)) {
          obj = tick;
        } else {
          let value = self.scale(tick);
          value = value >= 0 && value <= 1 ? value : NaN;

          obj = {
            text: isString(tick) ? tick : self.getText(tick),
            value,
            tickValue: tick // 
          };
        }
        rst.push(obj);
      });
      return rst;
    }
  });
}

export {
  updateLinearScale,
  updateCatScale
};

export default {
  updateLinearScale,
  updateCatScale
};
